








import {defineComponent, inject, onMounted, onUnmounted, ref} from '@vue/composition-api';
import {VsCode} from "@/types/VsCodeApi";

export default defineComponent({
   name: "JsonEditor",
   setup: function () {
      const textEditor = ref<HTMLTextAreaElement | null>(null);
      const data = ref('');

      // Get the API
      const vscode = inject('vscode') as VsCode;

      // Get the current state of the extension
      const state = vscode.getState();
      if (state) {
         updateContent(state.text);
      }

      // Enable tabs inside the textarea
      function enableTab(): void {
         const start: number = textEditor.value?.selectionStart ?? 0;
         const end: number = textEditor.value?.selectionEnd ?? 0;

         textEditor.value!.value = textEditor.value?.value.substring(0, start) +
             "\t" + textEditor.value?.value.substring(end);

         textEditor.value!.selectionStart = textEditor.value!.selectionEnd = start + 1;
      }

      // Check if JSON
      function isStringJson(str: string): boolean {
         try {
            JSON.parse(str);
         } catch {
            return false;
         }
         return true;
      }

      // Get messages from extension
      function getData(event: MessageEvent): void {
         if (event.data.type === 'vuejsoneditor.update') {
            const text = event.data.text;
            updateContent(text);
            vscode.setState({text});
         }
      }

      // Send messages to extension
      function sendData(): void {
         if (isStringJson(data.value)) {
            vscode.postMessage({type: 'vuejsoneditor.edit', content: data.value});
         } else {
            vscode.postMessage({type: 'vuejsoneditor.jsonError'});
         }
      }

      function updateContent(text: string): void {
         let json: string;
         try {
            if (!text) {
               text = '{}';
            }
            json = JSON.parse(text);
         } catch {
            console.error('No data was received because it is not valid json!');
            return;
         }

         data.value = JSON.stringify(json, undefined, 4);
      }

      onMounted(() => {
         window.addEventListener('message', getData);
      })

      onUnmounted(() => {
         window.removeEventListener('message', getData);
      })

      return {
         textEditor,
         data,
         enableTab,
         sendData,
      };
   }
})
